






















































































import { isValidUsername } from '@/utils/validate'
import { Component, Vue, Watch } from 'vue-property-decorator'
import { UserModule } from '@/store/modules/user'
import { Route } from 'vue-router'
import { Form as ElForm } from 'element-ui'
import { baseUrl } from '@/utils/config'
import SocialSign from './socialsignin.vue'

const validateUsername = (rule: any, value: string, callback: any) => {
  if (!isValidUsername(value)) {
    callback(new Error('请输入正确的用户名'))
  } else {
    callback()
  }
}
const validatePass = (rule: any, value: string, callback: any) => {
  if (value.length < 3) {
    callback(new Error('密码不能小于3位'))
  } else {
    callback()
  }
}

const validatePassCaptcha = (rule: any, value: string, callback: any) => {
  if (value.length === 0) {
    callback(new Error('请输入验证码'))
  } else {
    callback()
  }
}

@Component({
  components: {
    SocialSign
  }
})
export default class Login extends Vue {
  private loginForm = {
    username: 'admin',
    password: 'admin12',
    captcha: ''
  };
  private loginRules = {
    username: [{ required: true, trigger: 'blur', validator: validateUsername }],
    password: [{ required: true, trigger: 'blur', validator: validatePass }],
    captcha: [{ required: true, trigger: 'blur', validator: validatePassCaptcha }]
  };
  private loading = false;
  private pwdType = 'password';
  private redirect: string | undefined = undefined;
  /** 验证码 */
  private captchaSrc: string = baseUrl + '/captcha.jpg?t=' + Date.now();
  /** 是否显示第三方登陆 */
  private showDialog: boolean = false

  @Watch('$route', { immediate: true })
  private OnRouteChange(route: Route) {
    // TODO: remove the "as string" hack after v4 release for vue-router
    // See https://github.com/vuejs/vue-router/pull/2050 for details
    this.redirect = route.query && route.query.redirect as string
  }

  private showPwd() {
    if (this.pwdType === 'password') {
      this.pwdType = ''
    } else {
      this.pwdType = 'password'
    }
  }

  private handleLogin() {
    (this.$refs.loginForm as ElForm).validate((valid: boolean) => {
      if (valid) {
        this.loading = true
        UserModule.Login(this.loginForm).then(() => {
          this.loading = false
          this.$router.push({ path: this.redirect || '/' })
        }).catch(() => {
          this.loading = false
          this.refreshCode()
          this.loginForm.captcha = ''
        })
      } else {
        return false
      }
    })
  }

  /** 刷新验证码 */
  private refreshCode(): void {
    this.captchaSrc = baseUrl + '/captcha.jpg?t=' + Date.now()
  }
}
