














import { Component, Vue } from 'vue-property-decorator'
import openWindow from '@/utils/openWindow'
import { fetchWechatQrcode, fetchQQQrcode, fetchSinaQrcode } from '@/api/login'

@Component({
  name: 'SocialSignin'
})
export default class SocialSignin extends Vue {
  private wechatHandleClick(thirdpart: string) {
    // alert('ok')
    // this.$store.commit('SET_AUTH_TYPE', thirdpart)
    // const appid = 'xxxxx'
    // const redirect_uri = encodeURIComponent('xxx/redirect?redirect=' + window.location.origin + '/auth-redirect')
    // const url = 'https://open.weixin.qq.com/connect/qrconnect?appid=' + appid + '&redirect_uri=' + redirect_uri + '&response_type=code&scope=snsapi_login#wechat_redirect'
    fetchWechatQrcode().then((response: any) => {
      openWindow(response.url, thirdpart, 540, 620)
      // window.open(response.url)
    })
  }
  private tencentHandleClick(thirdpart: string) {
    // alert('ok')
    // this.$store.commit('SET_AUTH_TYPE', thirdpart)
    // const client_id = 'xxxxx'
    // const redirect_uri = encodeURIComponent('xxx/redirect?redirect=' + window.location.origin + '/auth-redirect')
    // const url = 'https://graph.qq.com/oauth2.0/authorize?response_type=code&client_id=' + client_id + '&redirect_uri=' + redirect_uri
    // openWindow(url, thirdpart, 540, 540)
    fetchQQQrcode().then((response: any) => {
      // openWindow(response.url, thirdpart, 540, 540)
      window.open(response.url)
    })
  }
  private sinaHandleClick(thirdpart: string) {
    fetchSinaQrcode().then((response: any) => {
      window.open(response.url)
    })
  }
}
